import React from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import './scss/style.scss';

import RouteWithLayout from './containers/RouteWithLayout';

// Containers
const TheLayout = React.lazy(() => import('./containers/TheLayout'));

// Pages
const Home = React.lazy(() => import('./views/home/Home'));
const Page404 = React.lazy(() => import('./views/page404/Page404'));
const Products = React.lazy(() => import('./views/product/Product'));
const Finish = React.lazy(() => import('./views/finish/Finish'));
const FinishCart = React.lazy(() => import('./views/finishCart/Finish'));

const Routes = () => {

  return (
    <Switch>
      <Redirect exact from="/" to="/home/:numero" />
      <RouteWithLayout exact path="/home/:numero" component={Home} layout={TheLayout} />
      <RouteWithLayout exact path="/products/:categorie/:empresa" component={Products} layout={TheLayout} />
      <RouteWithLayout exact path="/finishcart" component={FinishCart} layout={TheLayout} restricted />
      <Route exact path="/finish" render={props => <Finish {...props} />} />
      <Route exact path="/404" name="Page 404" render={props => <Page404 {...props} />} />
      <Redirect exact from="/" to="/404" />
    </Switch>
  );
}

export default Routes;
