import types from './constants';

const initial_state = {
  form: { produtos_pedidos: [] }
};

export default function FormReducer (state = initial_state, action) {

  switch (action.type) {
    case types.ADD_INFO_FORM:
      return {
        ...state,
        form: action.payload
      };
    case types.CLEAR_FORM:
      return initial_state;
    default:
      return state;
  }
};
